.flashCard {
  max-width: 400px;
  width: 100%;
  margin: 0 auto;
  background-color: #dad8d8;
  /* padding: 1rem; */
  border-radius: 5px;

  text-align: left;
}
.word,
.option {
  cursor: pointer;
}
.word {
  padding-bottom: 0.5rem;
  padding-top: 0.5rem;
  padding-left: 0.9rem;
  font-weight: bold;

  border: 1px solid #a8a0a0;
  border-radius: 5px;
}
.meaning,
.sentence {
  padding-bottom: 0.5rem;
  padding-top: 0.5rem;
  padding-left: 0.9rem;
  /* font-weight:; */

  border: 1px solid #a8a0a0;
  border-radius: 5px;
}
.option {
  padding-bottom: 0.5rem;
  padding-top: 0.5rem;
  padding-left: 0.9rem;
  font-size: 0.8rem;

  border: 1px solid #a8a0a0;
  border-radius: 5px;
  user-select: none;
}
.option:hover {
  background-color: #c54b70;
}
.message {
  padding-bottom: 0.5rem;
  padding-top: 0.5rem;
  padding-left: 0.9rem;
  font-size: 0.8rem;
  color: white;
  background-color: #a43557;
  text-align: center;

  border: 1px solid #a8a0a0;
  border-radius: 5px;
}
.next-btn {
  margin: 0.1rem 7rem;
}
