.body-area {
  display: grid;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}
.create-section {
  margin-top: 25px;
}
.form-container {
  max-width: 400px;
  width: 100%;
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
  background-color: var(--navyblue);
  border-radius: 0 0 5px 5px;
  border: 1px solid #3a3e59;

  -webkit-box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
  -box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
}
.title {
  max-width: 400px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.3rem;

  padding: 10px;
  border-radius: 5px 5px 0 0px;
}
.title {
  background-color: #255784;
  color: #ed6b5b;
  user-select: none;

  -webkit-box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
  -box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
}
.message {
  color: white;
  user-select: none;

  -webkit-box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
  -box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
}
.red-message {
  background-color: var(--darkpink);
}
.green-message {
  background-color: rgb(94, 153, 7) !important;
}

.theory {
  display: grid;
}
.lab label,
.theory label {
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 1.2rem;
  text-align: center;
  color: #ed6b5b;
  user-select: none;
}

.theory input {
  font-size: 1.1rem;
  padding: 0.4rem 0.2rem;
  user-select: none;
  border-radius: 5px;
  /* border: 1px solid rgb(185, 28, 0); */
  border: 1px solid #3a3e59;
  background-color: #c36b84;
}
.type {
  font-size: 1rem;
  padding: 0.2rem 0.2rem;
  user-select: none;
  cursor: pointer;
  border-radius: 5px;
  /* border: 1px solid rgb(185, 28, 0); */
  border: 1px solid #3a3e59;
  background-color: #c36b84;
}

textarea {
  user-select: none;
  font-size: 1.1rem;
  height: 4.8rem;
  padding: 0.4rem 0.2rem;
  border-radius: 5px;
  border: 1px solid #3a3e59;
  background-color: #c36b84;
}

.buttons {
  display: flex;
  justify-content: space-between;
}
.w3-btn {
  font-size: 1.2rem;
  padding: 0.2rem 0.5rem;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  border-color: #3a3e59;
  background-color: #255784;
  color: rgb(209, 207, 207);
  cursor: pointer;
}
input[type="text"],
textarea:focus {
  background-color: #c36b84;
}
