.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
canvas {
  border: 1px solid;
}
.nav-container {
  margin: 0 auto;
}
nav {
  width: 400px;
  margin: 0 auto;
}
nav ul {
  list-style: circle;
  padding: 0 0 0 0rem;
  user-select: none;
  margin-left: 1rem;
}
nav ul li {
  padding: 0.2rem 0.8rem;
  margin: 0.1rem 0rem;
  background-color: #282c34;
}
nav ul li:hover {
  color: white;
}

nav ul li a:hover {
  color: rgb(216, 214, 214);
}
nav ul li a:focus {
  color: rgb(216, 214, 214);
}

nav ul a {
  text-decoration: none;
  color: var(--orange);
  padding: 0 70% 0 0;
  cursor: pointer;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
